@import url(https://fonts.googleapis.com/css2?family=PT+Sans:wght@400;700&display=swap);
*{padding:0;margin:0;border:0}*,*:before,*:after{box-sizing:border-box}:focus,:active{outline:none}a:focus,a:active{outline:none}nav,footer,header,aside{display:block}html,body{height:100%;width:100%;font-size:100%;line-height:1;font-size:14px;-ms-text-size-adjust:100%;-moz-text-size-adjust:100%;-webkit-text-size-adjust:100%}input,button,textarea{font-family:inherit}input::-ms-clear{display:none}button{cursor:pointer}button::-moz-focus-inner{padding:0;border:0}a,a:visited{text-decoration:none}a:hover{text-decoration:none}ul li{list-style:none}img{vertical-align:top}h1,h2,h3,h4,h5,h6{font-size:inherit;font-weight:400}body{font-family:'PT Sans', sans-serif;background-color:#fcdb00}

.wrapper{min-height:100vh;overflow:hidden;display:flex;flex-direction:column}.container{max-width:960px;margin:0 auto}.content{flex:1 1 auto}.title{font-size:32px;font-weight:bold;line-height:150%}.subtitle{font-size:20px;line-height:150%;color:#313841}.text{font-size:16px;line-height:150%;color:#555555}@media (max-width: 767px){.title{font-size:37px}.subtitle{font-size:25px}.text{font-size:21px}}

.footer{background-color:#010101;color:#fff;font-size:16px;line-height:150%;padding:70px 0px}@media (max-width: 767px){.footer{font-size:21px}}.footer__row{display:flex}@media (max-width: 767px){.footer__row{flex-direction:column;align-items:center}}.footer__contacts{flex:1 1 20%;margin:0px 30px 0px 0px}@media (max-width: 767px){.footer__contacts{order:2;text-align:center;margin:0}}.footer__menu{flex:1 1 80%;display:flex;justify-content:flex-end}@media (max-width: 767px){.footer__menu{order:1}}.footer__list li:hover{-webkit-transform:scale(1.1, 1.1);transform:scale(1.1, 1.1)}.footer__columns{display:flex}@media (max-width: 767px){.footer__columns{flex-direction:column}}.footer__column{margin-right:100px}.footer__column:last-child{margin-right:0}@media (max-width: 767px){.footer__column{line-height:150%;text-align:center;margin:0px 0px 20px 0px}}

.header{background-color:#fcdb00;padding:70px 0px}@media (max-width: 767px){.header{padding:10px 5px}}.header__row{display:flex;align-items:center;justify-content:center}@media (max-width: 767px){.header__row{margin-bottom:20px}}.header__logo{display:flex;align-items:center;flex:1 1 50%}.header__img{margin:0px 10px 0px 0px}.header__name{font-size:30px;font-weight:bold}.header__menu{flex:1 1 50%;display:flex;justify-content:flex-end;align-items:center;font-size:22px;font-weight:bold}@media (max-width: 767px){.header__menu{display:none}}.header__link{color:#000;margin-right:70px;transition-duration:0.2s;font-size:inherit;font-weight:inherit;background:transparent}.header__link:last-child{margin-right:0px}.header__link:hover{-webkit-transform:scale(1.1, 1.1);transform:scale(1.1, 1.1)}.header .header__mobile-menu{position:absolute;width:100%;height:100%;text-align:center;background-color:#fcdb00;top:60px;left:0;padding:40px 0px;z-index:2;-webkit-animation:mobile-menu 0.5s linear;animation:mobile-menu 0.5s linear;display:flex;flex-direction:column}.header .header__mobile-menu button{display:block;margin:0px 0px 50px 0px;font-size:35px;line-height:150%;font-weight:bold}.header .header__mobile-menu button:last-child{margin:0}@media (max-width: 767px){.header__burger{width:30px;display:grid;align-items:center;grid-template:repeat(3, 8px)/1fr;transition-duration:0.2s}.header__burger::before{content:" ";height:2px;width:30px;background-color:#010101}.header__burger span{height:2px;width:30px;background-color:#010101}.header__burger::after{content:" ";height:2px;width:30px;background-color:#010101}.header__burger:hover{-webkit-transform:scale(1.1, 1.1);transform:scale(1.1, 1.1)}.header__cross{width:30px;height:30px;position:relative;transition-duration:0.2s}.header__cross:hover{-webkit-transform:scale(1.1, 1.1);transform:scale(1.1, 1.1)}.header__cross::before{content:" ";position:absolute;left:0;top:15px;-webkit-transform:rotate(45deg);transform:rotate(45deg);height:2px;width:30px;background-color:#010101;-webkit-animation:cross-left-line 0.2s linear;animation:cross-left-line 0.2s linear}.header__cross::after{content:" ";position:absolute;left:0;top:15px;-webkit-transform:rotate(-45deg);transform:rotate(-45deg);height:2px;width:30px;background-color:#010101;-webkit-animation:cross-right-line 0.2s linear;animation:cross-right-line 0.2s linear}}@-webkit-keyframes mobile-menu{0%{height:0%}100%{height:100%}}@keyframes mobile-menu{0%{height:0%}100%{height:100%}}@-webkit-keyframes cross-left-line{0%{-webkit-transform:rotate(0deg);transform:rotate(0deg)}100%{-webkit-transform:rotate(45deg);transform:rotate(45deg)}}@keyframes cross-left-line{0%{-webkit-transform:rotate(0deg);transform:rotate(0deg)}100%{-webkit-transform:rotate(45deg);transform:rotate(45deg)}}@-webkit-keyframes cross-right-line{0%{-webkit-transform:rotate(0deg);transform:rotate(0deg)}100%{-webkit-transform:rotate(-45deg);transform:rotate(-45deg)}}@keyframes cross-right-line{0%{-webkit-transform:rotate(0deg);transform:rotate(0deg)}100%{-webkit-transform:rotate(-45deg);transform:rotate(-45deg)}}

.inspire{background-color:#fcdb00}.inspire__row{display:flex;padding-bottom:50px;justify-content:center}.inspire__page{display:flex;padding-bottom:50px;justify-content:center;position:relative;-webkit-animation:new-slide linear 0.7s;animation:new-slide linear 0.7s}@media (max-width: 767px){.inspire__page{flex-direction:column;text-align:center;padding-bottom:20px;align-items:center}}.inspire__body{margin:0px 100px 0px 0px;align-self:center}@media (max-width: 767px){.inspire__body{margin:0px 0px 20px 0px}}.inspire__title{margin-bottom:20px}.inspire__subtitle{margin-bottom:20px}.inspire__text{margin-bottom:40px}.inspire__icons{display:flex;font-size:42px}.inspire__icons li{margin-right:40px;transition-duration:0.2s}.inspire__icons li:last-child{margin-right:0}.inspire__icons li:hover{-webkit-transform:scale(1.1, 1.1);transform:scale(1.1, 1.1)}@media (max-width: 767px){.inspire__icons{justify-content:center}}.inspire__tablet{position:relative;max-width:320px;max-height:470px;background-color:white}.inspire__dots{display:flex;width:55px;justify-content:space-between}@media (max-width: 767px){.inspire__dots{align-items:center;width:120px}}.inspire__dot{width:15px;height:15px;background-color:rgba(255,255,255,0.5);border-radius:50%}.inspire__dot:hover{-webkit-transform:scale(1.1, 1.1);transform:scale(1.1, 1.1)}.inspire__dot-active{background-color:white}@-webkit-keyframes new-slide{0%{bottom:20px}100%{bottom:0px}}@keyframes new-slide{0%{bottom:20px}100%{bottom:0px}}

.features{padding:160px 0px;background-color:white}@media (max-width: 767px){.features{padding:20px 0px}}.features__row{display:flex;flex-direction:column}.features__title{text-align:center;margin-bottom:20px}.features__subtitle{text-align:center;margin-bottom:90px}@media (max-width: 767px){.features__subtitle{margin-bottom:20px}}.features__columns{display:flex;margin:0px -20px}@media (max-width: 767px){.features__columns{margin:0px;flex-direction:column}}.features__column{padding:0px 20px}@media (max-width: 767px){.features__column{padding:0px}}.features__items{display:flex;flex-direction:column;margin:-40px 0px}@media (max-width: 767px){.features__items{margin:0px;align-items:center;justify-content:center}}.features__item{padding:40px 0px;display:flex}@media (max-width: 767px){.features__item{flex-direction:column;padding:5px;text-align:center}}.features__icon{display:inline-block;color:#010101;font-size:30px;margin-right:20px}@media (max-width: 767px){.features__icon{margin-right:10px}}.features__item-title{display:inline-block;font-size:22px;line-height:150%;font-weight:bold}

.in-touch{padding:100px 0px;background-color:#010101;color:white}@media (max-width: 767px){.in-touch{padding:10px 0px}}.in-touch__row{display:flex;flex-direction:column;align-items:center}.in-touch__title{text-align:center;margin-bottom:20px;color:#FCDB00}.in-touch__subtitle{color:#898989;text-align:center;margin-bottom:40px}@media (max-width: 767px){.in-touch__subtitle{margin-bottom:20px}}.in-touch__form{margin-bottom:80px}@media (max-width: 767px){.in-touch__form{margin-bottom:20px}}.in-touch__input{display:inline-block;margin-right:20px}@media (max-width: 767px){.in-touch__input{margin:0px;display:block}}.in-touch__input input{height:50px;width:600px;padding:20px;font-size:16px;line-height:150%;border-radius:5px;transition-duration:0.5s}.in-touch__input input:focus{box-shadow:0px 0px 20px #FCDB00}@media (max-width: 767px){.in-touch__input input{width:300px;margin-bottom:10px}}.in-touch__button{display:inline-block}@media (max-width: 767px){.in-touch__button{display:block;text-align:center}}.in-touch__button button{font-size:16px;line-height:150%;text-transform:uppercase;font-weight:bold;width:130px;height:50px;background-color:#FCDB00;border-radius:5px;color:#010101}.in-touch__button button:hover{background-color:rgba(252,219,0,0.8)}.in-touch__button button:active{background-color:#e8d636}.in-touch__socials{font-size:42px;display:flex;width:150px;justify-content:space-between;color:#fcdb00}.in-touch__facebook:hover{color:rgba(252,219,0,0.8)}.in-touch__facebook:active{color:#e8d636}.in-touch__twitter:hover{color:rgba(252,219,0,0.8)}.in-touch__twitter:active{color:#e8d636}.in-touch__google:hover{color:rgba(252,219,0,0.8)}.in-touch__google:active{color:#e8d636}

